import { mapState, mapMutations } from 'vuex'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl
    }
  },
  computed: {
    ...mapState([
      'getCoursePopUpState'
    ])
  },
  methods: {
    ...mapMutations({
      setGetCoursePopupState: 'SET_GET_COURSE_POPUP_STATE'
    }),
    // 关闭弹窗
    onShowStatusFn () {
      this.setGetCoursePopupState(false)
    }
  }
}
